<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getHourse"></el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <el-table :data="hourse" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="" width="120">
            </el-table-column>
            <el-table-column prop="acreage" label="" width="100">
            </el-table-column>
            <el-table-column prop="houseOwnerPhone" label="" width="150">
            </el-table-column>
            <el-table-column prop="status" label="" width="130" sortable>
            </el-table-column>
            <el-table-column prop="address" label="" min-width="150" >

            </el-table-column>
            <el-table-column label="" width="150">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
            </el-pagination>
        </el-col>

        <!---->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editForm.sex">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker type="date" placeholder="" v-model="editForm.birth"></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="editForm.addr"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>

        <!---->
        <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="addForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="addForm.sex">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-input-number v-model="addForm.age" :min="0" :max="200"></el-input-number>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker type="date" placeholder="" v-model="addForm.birth"></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="addForm.addr"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    //import NProgress from 'nprogress'
    import { getHourseListPage, deleteHourse, batchRemoveUser, editUser, addUser } from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    name: ''
                },
                users: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                //
                editForm: {
                    id: 0,
                    name: '',
                    sex: -1,
                    age: 0,
                    birth: '',
                    addr: ''
                },

                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                //
                addForm: {
                    name: '',
                    sex: -1,
                    age: 0,
                    birth: '',
                    addr: ''
                }

            }
        },
        methods: {
            //
            formatSex: function (row, column) {
                return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getHourse();
            },
            //
            getHourse() {
                let user = JSON.parse(sessionStorage.getItem('user'))
                let para = {
                    pageNumber: this.page - 1,
                    name: this.filters.name,
                    pageSize:20,
                    userId: user.id
                };
                this.listLoading = true;
                //NProgress.start();
                getHourseListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.hourse = res.data.data;
                    this.listLoading = false;
                    //NProgress.done();
                });
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {hourseId: row.id};
                    deleteHourse(para).then((res) => {
                        this.listLoading = false;
                        if(res.data.status == 200) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.getHourse();
                        }

                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.editForm);
                            para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            editUser(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getHourse();
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.addForm);
                            para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            addUser(para).then((res) => {
                                this.addLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addForm'].resetFields();
                                this.addFormVisible = false;
                                this.getHourse();
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = { ids: ids };
                    batchRemoveUser(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getHourse();
                    });
                }).catch(() => {

                });
            }
        },
        mounted() {
            this.getHourse();
        }
    }

</script>

<style scoped>

</style>


